	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login','setToken']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				uni.redirectTo({
					url: '/pages/public/register'
				})
			},
			async toLogin(){
				this.logining = true;
				const {mobile, password} = this;
				
				this.$http.post('/cms/token/login', {username:mobile,password:password}).then(res => {
					if(res.data.token){
						this.setToken(res.data.token);
						uni.navigateBack();
						uni.$emit('info',{msg:''})
					}else{
						this.$api.msg(res.info.status_err);
						this.logining = false;
					}
				}).catch(err => {});
				
			}
		},

	}
